import * as React from "react"
import { MainDiv, CustomMDXRender, ArticalHeader } from "../components"
import { Link, useStaticQuery, graphql } from 'gatsby'


// 会被 createPage 自动带入变量执行
export const ql = graphql`
  query ArticleQuery($id: String) {
    mdx(id: {eq: $id}) {
      body
      frontmatter {
        categories
        date(formatString: "YYYY-MM-DD kk:mm")
        draft
        tags
        title
      }
    }
  }
`


// ql 执行结果会传入 props 的 data
const PostPage = (props) => {
  console.log(props)
  // gatsby-node 把 graph 查询结果传递过来
  const val = props.data.mdx
  return (
    <MainDiv>
      <ArticalHeader>
        <h1>{val.frontmatter.title}{val.frontmatter.draft ? "（草稿）" : ""}</h1>
        <h3>{val.frontmatter.date}</h3>
      </ArticalHeader>
      <CustomMDXRender>
        {val.body}
      </CustomMDXRender>
    </MainDiv>
  )
}

export default PostPage;